<template>
  <div :class="className" :style="{height:height,width:width}" />
</template>

<script>
import echarts from "echarts";
require("echarts/theme/macarons"); // echarts theme
import resize from "@admin/mixins/resize";
import { ChinaMap } from "./lib/china";
ChinaMap(echarts);
import geoCoordMap from "./lib/geoCoordMap";
export default {
  mixins: [resize],
  props: {
    className: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "100%"
    }
  },
  data() {
    return {
      chart: null
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart() {
      const myChart = echarts.init(this.$el, "macarons");
      myChart.showLoading();
      var mapFeatures = echarts.getMap("china_map").geoJson.features;
      var GZData = [
        [
          {
            name: "太原"
          },
          {
            name: "北京",
            value: 95
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "上海",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "重庆",
            value: 70
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "西安",
            value: 60
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "成都",
            value: 50
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "宁波",
            value: 40
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "深圳",
            value: 30
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "郑州",
            value: 20
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "海口",
            value: 10
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "天津",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "内蒙古",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "哈尔滨",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "阿克苏地区",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "青海",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "西藏",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "兰州",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "长春",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "山东",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "辽宁",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "云南",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "台湾",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "厦门",
            value: 80
          }
        ],
        [
          {
            name: "太原"
          },
          {
            name: "大同",
            value: 80
          }
        ]
      ];
      var convertData = function(data) {
        var res = [];
        for (var i = 0; i < data.length; i++) {
          var dataItem = data[i];
          var fromCoord = geoCoordMap[dataItem[0].name];
          var toCoord = geoCoordMap[dataItem[1].name];
          if (fromCoord && toCoord) {
            res.push({
              fromName: dataItem[0].name,
              toName: dataItem[1].name,
              coords: [fromCoord, toCoord]
            });
          }
        }
        return res;
      };

      var option = {
        tooltip: {
          trigger: "item"
        },
        geo: {
          map: "china_map",
          label: {
            emphasis: {
              show: false
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              borderColor: "rgba(147, 235, 248, 1)",
              borderWidth: 1,
              areaColor: {
                type: "radial",
                x: 0.5,
                y: 0.5,
                r: 0.8,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(175,238,238, 0)" // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(47,79,79, .1)" // 100% 处的颜色
                  }
                ],
                globalCoord: false // 缺省为 false
              },
              shadowColor: "rgba(128, 217, 248, 1)",
              // shadowColor: 'rgba(255, 255, 255, 1)',
              shadowOffsetX: -2,
              shadowOffsetY: 2,
              shadowBlur: 10
            },
            emphasis: {
              areaColor: "#389BB7",
              borderWidth: 0
            }
          }
        },
        series: [
          {
            name: "太原",
            type: "lines",
            zlevel: 2,
            symbol: ["none", "arrow"],
            symbolSize: 10,
            effect: {
              show: true,
              period: 6,
              trailLength: 0,
              symbol: "arrow",
              symbolSize: 5
            },
            lineStyle: {
              normal: {
                color: "#c5f80e",
                width: 1,
                opacity: 0.6,
                curveness: 0.2
              }
            },
            data: convertData(GZData)
          },
          {
            name: "太原",
            type: "effectScatter",
            coordinateSystem: "geo",
            zlevel: 2,
            rippleEffect: {
              brushType: "stroke"
            },
            label: {
              normal: {
                show: true,
                position: "right",
                formatter: "{b}"
              }
            },
            symbolSize: function(val) {
              return val[2] / 8;
            },
            itemStyle: {
              normal: {
                color: "#c5f80e"
              }
            },
            data: GZData.map(function(dataItem) {
              return {
                name: dataItem[1].name,
                value: geoCoordMap[dataItem[1].name].concat([dataItem[1].value])
              };
            })
          }
        ]
      };
      myChart.setOption(option);
      myChart.hideLoading();
      this.chart = myChart;
    }
  }
};
</script>
